* {
  box-sizing: border-box;
  background-color: #101820FF;
  background: #101820FF;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  color: #FEE715FF;
}

.token-list-container {
  display: -webkit-inline-flex;
  flex-wrap: wrap;
  justify-content: first baseline;
}

.token-list-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 130px;
  height: auto;
  margin: 0 0 0 0;
  padding: 1.1em;
  font-size: small;
}

.token-list-image {
  width: 160%;
}

.meta-mask-button {
  align-self: center;
  float: right;
}